

module MyWay.Services.Components {
    export interface ILibraryTechAgent {
        ContexteClient: ContexteClient.ContexteClientTechAgent;
    }
}

module MyWay.Services.ContexteClient {

    /**
     * Agent technique du contexte client
     */
    export class ContexteClientTechAgent extends ContexteClientAgent {

        /**
         * Récupération de l'ensemble des <b>id techniques</b> de tous les contextes clients (même ceux qui ne sont pas attachés à des clients!)
         * @returns Id technique de chaque contexte client
         */
        public getContextes(): IPromesse<string[]> {
            return this.sendMessage(ContextClientMessageSubject.GetContextes).then((pResult: Messages.ITechActionGetContextesResponse) => {
                return pResult.idContextes;
            });
        }

        /**
         * Destruction d'un contexte client. Chacun de ses processus sera alors détaché immédiatement
         * @throws Erreur si contexte client inexistant
         * @param pIdContexteClient Identifiant technique du contexte client à détruire
         * @returns true si destruction effective, false sinon
         */
        public detruireContexte(pIdContexteClient: string): IPromesse<boolean> {
            var lParameters: Messages.ITechActionDestroyContexteRequest = {
                idContexte: pIdContexteClient,
                expediteur: this.getExpediteur()
            };
            return this.sendMessage(ContextClientMessageSubject.DestroyContext, lParameters).then((pResult: Messages.ITechActionDestroyContexteResponse) => {
                return pResult.isDestroy;
            });
        }

        /**
         * Création d'un nouveau contexte client. Plusieurs cas sont envisagés vis-a-vis du paramètre pIdClient
         * <ul>
         *  <li>Si non fourni alors un nouveau contexte client sans id client sera retourné</li>
         *  <li>Si fourni et qu'un autre contexte client a déjà cet identifiant client alors aucun nouveau contexte client sera créé et le contexte client trouvé sera retourné</li>
         *  <li>Si fourni et qu'aucun autre contexte client ne possède déjà cet identifiant client alors un nouveau contexte client attaché à cet identifiant client sera créé et retourné</li>
         * </ul>
         * @param pIdClient [OPTIONNEL] Identifiant du client pour le nouveau contexte client
         * @returns Identifiant technique du nouveau contexte client créé
         */
        public nouveauContexte(pIdClient?: string): IPromesse<string> {
            var lParameters: Messages.ITechActionNewContextRequest = {
                idClient: pIdClient,
                expediteur: this.getExpediteur()
            };
            return this.sendMessage(ContextClientMessageSubject.NewContext, lParameters).then((pResult: Messages.ITechActionNewContextResponse) => {
                return pResult.idContexte;
            });
        }

        /**
         * Change le contexte client d'un processus. Passer "null" comme id contexte client pour retirer le processus de tout contexte
         * @throws Erreur si le processus n'existe pas
         * @throws Erreur si le contexte client n'existe pas
         * @param pIdContexteClient Identifiant du contexte client à assigner au processus. Peut être null
         * @param pCodeProcessus Code du processus à assigner au contexte client
         */
        public changerContexteDuProcessus(pIdContexteClient: string, pCodeProcessus: string): IPromesse<void> {
            var lParameters: Messages.ITechActionChangeContextProcessRequest = {
                idContexte: pIdContexteClient,
                codeProcessus: pCodeProcessus,
                expediteur: this.getExpediteur()
            };
            return this.sendMessage<void>(ContextClientMessageSubject.ChangeContextProcess, lParameters);
        }

        /**
         * Changer le mode de fonctionnement du contexte client. Attention RAPPEL : En mode automatique le contexte client se supprime de lui-même s'il n'est associé à aucun processus
         * @throws Erreur si le processus n'existe pas
         * @param pIdContexteClient Identifiant du contexte client à assigner au processus
         * @param pMode Nouveau mode du contexte client
         */
        public changerModeContexteClient(pIdContexteClient: string, pMode: Messages.ContexteClientMode): IPromesse<void> {
            var lParameters: Messages.ITechActionChangeContextModeRequest = {
                idContexte: pIdContexteClient,
                mode: pMode
            };
            return this.sendMessage<void>(ContextClientMessageSubject.ChangeContextMode, lParameters);
        }

        /**
         * Récupérer tous les détails d'un contexte
         * @throws Erreur si le processus n'existe pas
         * @param pIdContexteClient Identifiant du contexte client à traiter
         * @returns Renvois tous les détails du processus demandé
         */
        public getDetailsContexte(pIdContexteClient: string): IPromesse<Messages.ITechActionGetDetailsContextResponse> {
            var lParameters: Messages.ITechActionGetDetailsContextRequest = {
                idContexte: pIdContexteClient
            };
            return this.sendMessage(ContextClientMessageSubject.DetailsContext, lParameters);
        }

        /**
         * Modification de l'identifiant client d'un contexte spécifique
         * @throws Erreur si le contexte client n'existe pas
         * @throws Erreur si code client non valide
         * @throws Erreur si le contexte client a déjà un code client
         * @param pIdContexteClient Identifiant du contexte client à traiter
         * @param pClient Code du client à assigner au contexte client
         */
        public setClientOfContexteSpecifique(pIdContexteClient: string, pClient: string): IPromesse<void> {
            var lParameters: IContextClientMessageContentSetClientRequest = {
                idContexteClient: pIdContexteClient,
                client: pClient,
                expediteur: this.getExpediteur()
            };
            return this.sendMessage<void>(ContextClientMessageSubject.SetClient, lParameters);
        }

        /**
         * Lis une donnée d'un contexte spécifique
         * @throws Erreur si le contexte client n'existe pas
         * @param pIdContexteClient Identifiant du contexte client à traiter
         * @param pCle Clé à récupérer
         * @returns Valeur cherchée
         */
        public getPropertyOfContexteSpecifique(pIdContexteClient: string, pCle: string): IPromesse<any> {
            var lParameters: IContextClientMessageContentGetRequest = {
                idContexteClient: pIdContexteClient,
                cle: pCle
            };
            return this.sendMessage(ContextClientMessageSubject.GetProperty, lParameters).then((pResult: IContextClientMessageContentGetResponse) => {
                return pResult.valeur;
            });
        }

        /**
         * Ajoute une donnée dans un tableau d'un contexte spécifique
         * @throws Erreur si le contexte client n'existe pas
         * @param pIdContexteClient Identifiant du contexte client à traiter
         * @param pCle Clé à modifier
         * @param pValeur Valeur à ajouter
         * @returns true si valeur ajoutée, false sinon
         */
        public addToListOfContexteSpecifique(pIdContexteClient: string, pCle: string, pValeur: any): IPromesse<boolean> {
            var lParameters: IContextClientMessageContentAddRequest = {
                idContexteClient: pIdContexteClient,
                cle: pCle,
                valeur: pValeur,
                expediteur: this.getExpediteur()
            };
            return this.sendMessage(ContextClientMessageSubject.AddToList, lParameters).then((pResult: IContextClientMessageContentAddResponse) => {
                return pResult.estOk;
            });
        }

        /**
         * Fixe une donnée dans un objet d'un contexte spécifique
         * @throws Erreur si le contexte client n'existe pas
         * @param pIdContexteClient Identifiant du contexte client à traiter
         * @param pCle Clé à modifier
         * @param pValeur Valeur à placer
         * @returns true si valeur modifiée, false sinon
         */
        public setPropertyOfContexteSpecifique(pIdContexteClient: string, pCle: string, pValeur: any): IPromesse<boolean> {
            var lParameters: IContextClientMessageContentSetRequest = {
                idContexteClient: pIdContexteClient,
                cle: pCle,
                valeur: pValeur,
                expediteur: 'tech-agent'
            };
            return this.sendMessage(ContextClientMessageSubject.SetProperty, lParameters).then((pResult: IContextClientMessageContentSetResponse) => {
                return pResult.estOk;
            });
        }

        /**
         * Supprime une donnée d'un tableau d'un contexte spécifique
         * @throws Erreur si le contexte client n'existe pas
         * @param pIdContexteClient Identifiant du contexte client à traiter
         * @param pCle Clé à supprimer
         * @returns true si valeur supprimée, false sinon
         */
        public removeFromListOfContexteSpecifique(pIdContexteClient: string, pCle: string): IPromesse<boolean> {
            var lParameters: IContextClientMessageContentRemoveRequest = {
                idContexteClient: pIdContexteClient,
                cle: pCle,
                expediteur: this.getExpediteur()
            };
            return this.sendMessage(ContextClientMessageSubject.RemoveFromList, lParameters).then((pResult: IContextClientMessageContentRemoveResponse) => {
                return pResult.estOk;
            });
        }

        /**
         * Supprime une donnée d'un objet d'un contexte spécifique
         * @throws Erreur si le contexte client n'existe pas
         * @param pIdContexteClient Identifiant du contexte client à traiter
         * @param pCle Clé à supprimer
         * @returns true si valeur supprimée, false sinon
         */
        public deletePropertyOfContexteSpecifique(pIdContexteClient: string, pCle: string): IPromesse<boolean> {
            var lParameters: IContextClientMessageContentDeleteRequest = {
                idContexteClient: pIdContexteClient,
                cle: pCle,
                expediteur: this.getExpediteur()
            };
            return this.sendMessage(ContextClientMessageSubject.DeleteProperty, lParameters).then((pResult: IContextClientMessageContentDeleteResponse) => {
                return pResult.estOk;
            });
        }
    }

    Components.RegistryAgent.getInstance().register(ContexteClientTechAgent);
}