module MyWay.Services {
    export interface TechAgentExtended {
        TraceTechAgent: Trace.TraceTechAgent;

        purgerTraces: () => IPromesse<any>;
    }

    Core.defineProperty(Services.TechAgentExtended, 'TraceTechAgent', {
        get: function () {
            return (<Services.TechAgent>this)._techLibrary.TraceTechAgent
        }
    });

    Services.TechAgentExtended.prototype.purgerTraces = function (): IPromesse<any> {
        return (<Trace.TraceTechAgent>(<Services.TechAgent>this).Library.Trace).purgeTrace();
    }
}