

module MyWay.Services.Components {
    export interface ILibraryTechAgent {
        Habilitation: Habilitation.HabilitationTechAgent
    }
}

/**
* Module de gestion technique des habilitations
* @module MyWay.Services.Habilitation
* classe : HabilitationTechAgent - public - Classe permettant l'administration des habilitations
*/
module MyWay.Services.Habilitation {

    /**
    * Classe permettant la gestion des habilitations
    */
    export class HabilitationTechAgent extends HabilitationAgent {

        /**
        * Constructeur de la classe HabilitationTechAgent
        */
        constructor() {
            super();
        }

        public init(pAgentLibrary: Components.ILibraryAgent, pMessageService: Message.IMessageService, pMessageInitializer: Components.IAgentInitializer): IPromesse<void> {
            //Fixme Problème de compatibilité ascendante, le "Habiliation" tech agent a un nom différent du "Habilitation" agent
            pAgentLibrary["HabilitationTech"] = this;
            return super.init(pAgentLibrary, pMessageService, pMessageInitializer);
        }


        /**
         * Renvoie toutes les habilitations disponibles, dans un tableau d'habilitations
         * @return {IPromesse<HabilitationModel[]>} [description]
         */
        ObtenirHabilitations(): IPromesse<{ [key: string]: boolean }> {
            return this.sendMessage(HabilitationMessageSubject.ObtenirHabilitations, <IHabilitationMessageContentObtenirHabilitations>{});
        }

        /**
         * Permet de récupérer une habilitation au format HabilitationModel
         * @param  {string}                       code [description]
         * @return {IPromesse<HabilitationModel>}                  [description]
         */
        ObtenirHabilitation(code: string): IPromesse<HabilitationModel> {
            return this.sendMessage(HabilitationMessageSubject.ObtenirHabilitation, <IHabilitationMessageContentObtenirHabilitation>{ code: code });
        }

        /**
         * Ajoute ou modifie une habilitation
         * @param  {string}                       code Le code de l'habilitation à insérer / modifier
         * @param  {boolean}                      valeur           La valeur true/false de l'habilitation
         * @return {IPromesse<HabilitationModel>}                  [description]
         */
        ModifierHabilitation(code: string, valeur: boolean): IPromesse<HabilitationModel> {
            return this.sendMessage(HabilitationMessageSubject.ModifierHabilitation, <IHabilitationMessageContentModifierHabilitation>{ code: code, valeur: valeur });
        }

        /**
         * Charge les habilitations en écrasant toutes les modifications faites par l'utilisateur
         * @return {IPromesse<HabilitationModel[]>} [description]
         */
        ChargerHabilitations(): IPromesse<HabilitationModel> {
            return this.sendMessage(HabilitationMessageSubject.ChargerHabilitations, <IHabilitationMessageContentChargerHabilitations>{});
        }
    }

    Components.RegistryAgent.getInstance().register(HabilitationTechAgent);
} 
