module MyWay.Services {
    export interface TechAgentExtended {
        ContextTechAgent: Context.ContextTechAgent;

        AddToGlobalContext: (kvp: MyWay.Services.Context.IContextKeyValuePair[]) => IPromesse<any>;
        AddToTempoContext: (kvp: MyWay.Services.Context.IContextKeyValuePair[]) => IPromesse<any>;
        AddToMainProcessContext: (key: string, value: string) => IPromesse<any>;
        GetFromMainProcessContext: (key: string) => IPromesse<string>;
        GetCurrentMainProcess: () => IPromesse<Context.IProcess>;
    }

    Core.defineProperty(Services.TechAgentExtended, 'ContextTechAgent', {
        get: function () {
            return (<Services.TechAgent>this)._techLibrary.ContextTechAgent
        }
    });

    Services.TechAgentExtended.prototype.AddToGlobalContext = function (kvp: MyWay.Services.Context.IContextKeyValuePair[]): IPromesse<any> {
        return this._techLibrary.AddToGlobalContext(kvp);
    }

    Services.TechAgentExtended.prototype.AddToTempoContext = function (kvp: MyWay.Services.Context.IContextKeyValuePair[]): IPromesse<any> {
        return this._techLibrary.AddToTempoContext(kvp);
    }

    Services.TechAgentExtended.prototype.AddToMainProcessContext = function (key: string, value: string): IPromesse<any> {
        return this._techLibrary.AddToMainProcessContext(key, value);
    }

    Services.TechAgentExtended.prototype.GetFromMainProcessContext = function (key: string): IPromesse<string> {
        return this._techLibrary.GetFromMainProcessContext(key);
    }

    Services.TechAgentExtended.prototype.GetCurrentMainProcess = function (): IPromesse<Context.IProcess> {
        return this._techLibrary.GetCurrentMainProcess();
    }
}