
module MyWay.Services {
    'use strict';

    /*
    * Callback d'erreur générale
    */
    import Promise = Q.Promise;
    import PromiseState = Q.PromiseState;

    export interface ErreurCallback {
        (erreur: Erreur): void;
    }

    /*
    * Classe globale d'erreur pour renvoyer dans les callback d'erreur ou lever des exceptions
    */
    export class Erreur {
        /*
        * Code du message
        * Il s'agit du code libellé définissant l'erreur. Il peut servir à orienter des tests sur le type d'erreur ainsi qu'à appeler afficherMessage ou constituerLibelle du module Libelle
        */
        codeLibelle: string;

        /*
        * Valeurs de remplacement des placeholders
        */
        valeurs: string[];

        /*
        * Objet Error javascript si l'erreur vient d'une exception coté client
        */
        error: Error;

        /*
        * Code HTTP retourné par le serveur
        */
        codeHttp: number;

        /*
        * Libellé constitué au codeLibelle et aux valeurs
        */
        libelle: string;

        /*
        * Une liste d'erreurs à l'origine du problème
        */
        causes: Erreur[];

        constructor(codeLibelle: string, valeurs?: string[], causes?: Erreur[]) {
            this.codeLibelle = codeLibelle;
            this.valeurs = valeurs;
            this.causes = causes;
        }

    }

    /**
     * Fournit un contrat d'utilisation des promesses MyWay.
     *
     * Utilise le service de promesses d'AngularJS s'il est disponible ($q) et le service externe Q dans le cas contraire.
     *
     */
    export interface IServiceDePromesse {

        /**
         * ~~~ts
         * var promises: MyWay.Services.IPromesse<any>[] = [];
         * promises.push(serviceAgent.Context.AddToContext('foo', 'toto'));
         * promises.push(serviceAgent.Context.AddToContext('bar', "titi"));
         * MyWay.Services.Promesses.ServiceDePromesse.all(promises).then(() => {
         *     // actions à effectuer si les 2 promesses sont tenues
         *     ...
         * });
         * ~~~
         */
        all<T>(promises: IPromesse<T>[]): IPromesse<T[]>;
        all<T>(promises: any[]): IPromesse<T[]>;

        /**
         * Créé un objet permettant de valider ou rejeter la promesse demandée.
         *
         * ~~~ts
         * var deferred: IDeferred<string> = Promesses.ServiceDePromesse.defer<string>();
         * this.SendMessage(
         *     "GetFromContext",
         *     { key: key },
         *     (value: string) => { deferred.resolve(value); },
         *     (error?: Erreur) => { deferred.reject(error); }
         * );
         * return deferred.promise;
         * ~~~
         */
        defer<T>(erreurCallback?: ErreurCallback): IDeferred<T>;

        /**
         * Retourne une promesse qui est rejetée avec comme raison le paramètre passé.
         *
         */
        reject<T>(reason?: Erreur): IPromesse<T>;

        /**
         * Encapsule un objet qui peut être soit une valeur soit une promesse "chainable" dans une promesse $q ou Q.
         * 
         * Cela peut être utile lorsque on a affaire à un objet qui peut (ou ne pas) être une promesse, ou si la promesse provient d'une source qui ne peut pas être certifiée.
         */
        when<T>(value: T): IPromesse<T>;
    }

    export interface IPromesse<T> {

        /**
         * La méthode "then" fournit les callbacks qui recoivent 
         * 
         * - soit la valeur actuelle (ou éventuelle) d'une promesse (fulfill), 
         * 
         * - soit la raison pour laquelle cette promesse ne peut pas être tenue (reject).
         *
         * ~~~ts
         *serviceAgent.Context.GetFromContext('toto').then((value: string) => {
         *    // traitement du retour de promesse tenue (value)
         *    ...
         * });
         * ~~~
         * 
         */
        then<TResult>(onFulfill: (value: T) => IPromesse<TResult> | TResult, onReject?: (reason: Erreur) => any, onProgress?: Function): IPromesse<TResult>;
        then<TResult>(onFulfill: (value: T) => IPromesse<TResult> | TResult, onReject?: (reason: Erreur) => TResult, onProgress?: Function): IPromesse<TResult>;

        /**
         * Cette méthode est un alias du callback "onReject" de la méthode "then": promise.then(null, errorCallback)
         *
         * ~~~ts
         * serviceAgent.Context.GetFromContext('toto').then((value: string) => {
         *     // traitement du retour de promesse tenue
         *     ...
         * }).catch((erreur: MyWay.Services.Erreur) => {
         *     // traitement du retour de promesse non tenue (erreur)
         *     ...
         * });
         * ~~~
         */
        catch<TResult>(onRejected: (reason: Erreur) => IPromesse<TResult> | TResult): IPromesse<TResult>;

        /**
         * Permet d'observer le succès ou le rejet d'une promesse, mais sans modifier la valeur finale.
         *
         * Cette méthode est utile pour libérer des resources ou effectuer un nettoyage peu importe le résultat de la promesse.
         *
         * ~~~ts
         * serviceAgent.Context.GetFromContext('toto').then((value: string) => {
         *     // traitement du retour de promesse tenue
         *     ...
         * }).catch((erreur: MyWay.Services.Erreur) => {
         *     // traitement du retour de promesse non tenue (erreur)
         *     ...
         * }).finally(() => {
         *     // cleanup
         *     serviceAgent.Dispose();
         *     serviceAgent = null;
         * });
         * ~~~
         */
        finally(finallyCallback: () => any): IPromesse<T>;
    }

    /**
     * Objet retourné par la méthode "defer" permettant de valider ou rejeter la promesse demandée.
     */
    export interface IDeferred<T> {

        /**
         * Cette méthode controle l'état de la propriété "promise" dans le cas où la promesse est tenue.
         * 
         * Dans le cas où une promesse est tenue, sa résolution est permanente et ne peut pas être résolue une seconde fois.
         *
         * ~~~ts
         * var deferred: IDeferred<string> = Promesses.ServiceDePromesse.defer<string>();
         * this.SendMessage(
         *     "GetFromContext",
         *     { key: key },
         *     (value: string) => {
         *         deferred.resolve(value);
         *     },
         *     null
         * );
         * return deferred.promise;
         * ~~~
         */
        resolve(value?: T): void;

        /**
         * Cette méthode controle l'état de la propriété "promise" dans le cas où la promesse n'est pas tenue.
         *
         * On peut donner une raison de refus lors de l'appel à cette méthode. Dans notre cas une raison de type "Erreur".
         *
         * ~~~ts
         * var deferred: IDeferred<string> = Promesses.ServiceDePromesse.defer<string>();
         * this.SendMessage(
         *     "GetFromContext",
         *     { key: key },
         *     (value: string) => { 
         *         deferred.resolve(value); 
         *     }, // resolve
         *     (error?: Erreur) => {
         *         deferred.reject(error);
         *     }
         * );
         * return deferred.promise;
         * ~~~
         */
        reject(reason?: Erreur): void;

        /**
         * L'appel à cette méthode avec une valeur va notifier la promesse de cette valeur. 
         * 
         * Tout handler de type "onProgress" abonné à notre promesse réceptionnera cette valeur de progression (ou d'état).
         *
         */
        notify(state?: any): void;
        promise: IPromesse<T>;
    }

    export class MonServiceDePromesse implements IServiceDePromesse {
        lastReason: Erreur;
        constructor(private innerService: IServiceDePromesse) {

        }
        all<T>(promises: IPromesse<T>[]): IPromesse<T[]>;
        all<T>(promises: any[]): IPromesse<T[]>;
        all<T>(param: any): IPromesse<T[]> {
            return this.innerService.all(param);
        }

        defer<T>(erreurCallback?: ErreurCallback): IDeferred<T> {
            return new MonDeferred(this.innerService.defer(), (erreur) => {
                if (erreurCallback)
                    erreurCallback(erreur);
            });
        }
        reject<T>(reason?: Erreur): IPromesse<T> {
            return this.innerService.reject(reason);
        }
        when<T>(value: T): IPromesse<T> {
            return this.innerService.when(value);
        }
    }

    export class MaPromesse<T> implements IPromesse<T> {
        lastReason: Erreur;
        private _catchHappened: boolean = false;
        constructor(private innerPromesse: IPromesse<T>, private erreurCallback: ErreurCallback) {
        }
        then<TResult>(onFulfill: (value: T) => IPromesse<TResult>, onReject?: (reason: Erreur) => any, onProgress?: Function): IPromesse<TResult>;
        then<TResult>(onFulfill: (value: T) => IPromesse<TResult>, onReject?: (reason: Erreur) => TResult, onProgress?: Function): IPromesse<TResult>;
        then<TResult>(onFulfill: (value: T) => TResult, onReject?: (reason: Erreur) => TResult, onProgress?: Function): IPromesse<TResult>;
        then<TResult>(onFulfill, onReject?, onProgress?): IPromesse<TResult> {
            return this.innerPromesse.then<TResult>(onFulfill, onReject, onProgress);
        }

        catch<TResult>(onRejected: (reason: Erreur) => TResult): IPromesse<TResult>;
        catch<TResult>(onRejected: (reason: Erreur) => IPromesse<TResult>): IPromesse<TResult>;
        catch<TResult>(onRejected: any): IPromesse<TResult> {
            this._catchHappened = true;
            return this.innerPromesse.catch<TResult>(onRejected);
        }
        finally(finallyCallback: () => any): IPromesse<T> {
            if (!this._catchHappened && this.erreurCallback)
                this.erreurCallback(this.lastReason);
            return this.innerPromesse.finally(finallyCallback);
        }
    }

    export class MonDeferred<T> implements IDeferred<T>{
        promesse: MaPromesse<T>;
        constructor(private innerDeferred: IDeferred<T>, private erreurCallback: ErreurCallback) {
            this.promesse = new MaPromesse<T>(this.innerDeferred.promise, this.erreurCallback);
        }
        resolve = (value?: T) => {
            return this.innerDeferred.resolve(value);

        }
        reject = (reason?: Erreur) => {
            this.promesse.lastReason = reason;
            return this.innerDeferred.reject(reason);
        }
        notify = (state?: any) => {
            return this.innerDeferred.notify(state);
        }
        get promise(): IPromesse<T> {
            return this.promesse;
        }
    }

    interface IPromesseStatusResult {
        result: any;
        isError: boolean;
    }

    export class Promesses {
        private static ServiceDePromesse: IServiceDePromesse;
        private static promesseSelfResolved: IPromesse<void> = null;

        public static getServiceDePromesse(): IServiceDePromesse {
            if (!Promesses.ServiceDePromesse) {
                // promesses
                if (typeof angular != 'undefined') {
                    Promesses.ServiceDePromesse = new MonServiceDePromesse(<ng.IQService>angular.injector(['ng']).get('$q'));
                } else {
                    console.debug("[Framework Technique MyWay - Promesses - init] Angular n'est pas disponible");
                    Promesses.ServiceDePromesse = new MonServiceDePromesse(Q);
                }
            }
            return Promesses.ServiceDePromesse;
        }

        public static getPromesseSelfResoved(): IPromesse<void> {
            if (!Promesses.promesseSelfResolved) {
                var lDefered: IDeferred<void> = Promesses.getServiceDePromesse().defer<void>();
                lDefered.resolve();
                Promesses.promesseSelfResolved = lDefered.promise;
            }
            return Promesses.promesseSelfResolved;
        }

        public static createPromiseSelfResolved<T>(pValue?: T): IPromesse<T> {
            var lDefered: IDeferred<T> = Promesses.getServiceDePromesse().defer<T>();
            lDefered.resolve(pValue);
            return lDefered.promise;
        }

        public static createPromiseSelfError<T>(pError: Erreur): IPromesse<T> {
            var lDefered: IDeferred<T> = Promesses.getServiceDePromesse().defer<T>();
            lDefered.reject(pError);
            return lDefered.promise;
        }

        public static promesseTryCatch<T>(pCallback: (pDefer: IDeferred<T>) => any, pCallbackErreur?: (pErreur: Erreur, pDefer: IDeferred<T>) => any): IPromesse<T> {
            var lDefer: IDeferred<T> = Promesses.getServiceDePromesse().defer();
            try {
                pCallback(lDefer);
            }
            catch (e) {
                if (pCallbackErreur) {
                    pCallbackErreur(e, lDefer);
                } else {
                    lDefer.reject(e);
                }
            }
            return lDefer.promise;
        }

        public promesseTryCatch<T>(pCallback: (pDefer: IDeferred<T>) => any, pCallbackErreur?: (pErreur: Erreur, pDefer: IDeferred<T>) => any): IPromesse<T> {
            return Promesses.promesseTryCatch(pCallback, pCallbackErreur);
        }

        public static executePromisesSequentially(pPromises: Array<() => IPromesse<any>>): IPromesse<void> {
            return Promesses.promesseTryCatch((pDefer: IDeferred<void>) => {
                var lCallback = (pIndex: number) => {
                    try {
                        if (pPromises.length <= pIndex) {
                            pDefer.resolve();
                            return;
                        }
                        pPromises[pIndex]().then(() => {
                            lCallback(pIndex + 1);
                        }, (pError: Erreur) => {
                            pDefer.reject(pError);
                        });
                    } catch (e) {
                        pDefer.reject(new Erreur(e));
                    }
                };
                lCallback(0);
            });
        }

        public static executePromisesSimultaneous(pPromises: Array<IPromesse<any>>): IPromesse<void> {
            return Promesses.promesseTryCatch((pDefer: IDeferred<void>) => {
                Promesses.getServiceDePromesse().all(pPromises).then(() => { pDefer.resolve() }, pDefer.reject);
            });
        }

        public static all<T>(pPromises: Array<IPromesse<T>>): IPromesse<T[]> {
            return Promesses.getServiceDePromesse().all(pPromises);
        }

        public static executePromisesSimultaneousAndWaitAllResolves(pPromises: Array<IPromesse<any>>): IPromesse<void> {
            const lPromisesAAttendre: IPromesse<IPromesseStatusResult>[] = [];
            pPromises.forEach((pPromise: IPromesse<any>) => {
                lPromisesAAttendre.push(Promesses.promesseTryCatch((pPromiseFinally: IDeferred<IPromesseStatusResult>) => {
                    pPromise.then((result) => {
                        pPromiseFinally.resolve({
                            result: result,
                            isError: false
                        })
                    }, (e) => {
                        pPromiseFinally.resolve({
                            result: e,
                            isError: true
                        });
                    });
                }));
            });
            return Promesses.getServiceDePromesse().all(lPromisesAAttendre).then((pResults: IPromesseStatusResult[]) => {
                pResults.forEach((pPromiseResult: IPromesseStatusResult) => {
                    if (pPromiseResult.isError) {
                        throw pPromiseResult.result;
                    }
                });
            });
        }
    }
}