module MyWay.Services {
    export interface TechAgent {
        LisaTechAgent: Lisa.LisaTechAgent;

        StartProcess: (processCode: string, initData: MyWay.Services.Lisa.IInitialisationData, valeurDepart?: number) => IPromesse<Lisa.LisaCallbackResult>;
        StartSubProcess: (processCode: string, valeurDepart?: number) => IPromesse<Lisa.LisaCallbackResult>;
        StartExternalApp: (name: string, url: string, standAlone?: boolean) => IPromesse<void>;
        GetProcesses: () => IPromesse<Lisa.ProcessResult[]>;
        GetTools: () => IPromesse<Lisa.ApplicationResult[]>;
        GetCodesProcessus: () => IPromesse<string[]>;
        GetInfoProcessus: (codeProcessus: string) => IPromesse<Lisa.IInfoProcessus>;
    }

    Core.defineProperty(Services.TechAgent, 'LisaTechAgent', {
        get: function () {
            return (<Services.TechAgent>this)._techLibrary.LisaTechAgent
        }
    });
    
    Services.TechAgent.prototype.StartProcess = function (processCode: string, initData: MyWay.Services.Lisa.IInitialisationData, valeurDepart?: number): IPromesse<Lisa.LisaCallbackResult>{
        return (<Services.TechAgent>this).LisaTechAgent.StartProcess(processCode, initData, valeurDepart);
    }

    Services.TechAgent.prototype.StartSubProcess = function (processCode: string, valeurDepart?: number): IPromesse<Lisa.LisaCallbackResult> {
        return (<Services.TechAgent>this).LisaTechAgent.StartSubProcess(processCode, valeurDepart);
    }

    Services.TechAgent.prototype.StartExternalApp = function (name: string, url: string, standAlone?: boolean): IPromesse<void> {
        return (<Services.TechAgent>this).LisaTechAgent.StartExternalApp(name, url, standAlone);
    }

    Services.TechAgent.prototype.GetProcesses = function (): IPromesse<Lisa.ProcessResult[]> {
        return (<Services.TechAgent>this).LisaTechAgent.GetProcesses();
    }

    Services.TechAgent.prototype.GetTools = function (): IPromesse<Lisa.ApplicationResult[]> {
        return (<Services.TechAgent>this).LisaTechAgent.GetTools();
    }

    /**
    * Récupère les codes des processus LISA
    */
    Services.TechAgent.prototype.GetCodesProcessus = function (): IPromesse<string[]> {
        return (<Services.TechAgent>this).LisaTechAgent.GetCodesProcessus();
    }

    /**
    * Récupère les infos d'un processus LISA
    * @param {string}   codeProcessus  
    * code du processus
    */
    Services.TechAgent.prototype.GetInfoProcessus = function (codeProcessus: string): IPromesse<Lisa.IInfoProcessus> {
        return (<Services.TechAgent>this).LisaTechAgent.GetInfoProcessus(codeProcessus);
    }
}