module MyWay.Services {
    export interface TechLibrary {
        TraceSuiviTechAgent: TraceSuivi.TraceSuiviTechAgent;
    }

    Services.TechLibrary.addInitListener((pInstance: TechLibrary, library: Components.ILibraryTechAgent) => {
        pInstance.TraceSuiviTechAgent = library.TraceSuivi;
    });
}