module MyWay.Services.Components {
    export interface ILibraryTechAgent {
        Lisa: Lisa.LisaTechAgent;
    }
}

module MyWay.Services.Lisa {

    export class LisaTechAgent extends LisaAgent {
        constructor() {
            super();
        }

        public StartProcess(processCode: string, initData: MyWay.Services.Lisa.IInitialisationData, valeurDepart?: number): IPromesse<LisaCallbackResult> {
            return this.sendMessage(Messages.LisaMessageSubject.StartProcess, <Messages.ILisaMessageContentStartProcess>{
                processCode: processCode,
                initData: initData,
                valeurDepart: valeurDepart
            });
        }

        public StartSubProcess(processCode: string, valeurDepart?: number): IPromesse<LisaCallbackResult> {
            return this.sendMessage(Messages.LisaMessageSubject.StartSubProcess, <Messages.ILisaMessageContentStartSubProcess>{
                processCode: processCode,
                valeurDepart: valeurDepart
            });
        }

        public StartExternalApp(name: string, url: string, standAlone?: boolean): IPromesse<void> {
            return this.sendMessage(Messages.LisaMessageSubject.StartExternalApp, <Messages.ILisaMessageContentStartExternalApp>{
                name: name,
                url: url,
                standAlone: standAlone
            });
        }

         /* istanbul ignore next */
        public GetProcesses(): IPromesse<Lisa.ProcessResult[]> {
            return this.library.Core.createPromiseSelfError(new Erreur("Not implemented"));
        }

        public GetTools(): IPromesse<Lisa.ApplicationResult[]> {
            return this.sendMessage(Messages.LisaMessageSubject.GetTools, <Messages.ILisaMessageContentGetTools>{
            });
        }

        /**
        * Récupère les codes des processus LISA
        */
        public GetCodesProcessus(): IPromesse<string[]> {
            return this.sendMessage(Messages.LisaMessageSubject.GetCodesProcessus, <Messages.ILisaMessageContentGetCodesProcessus>{
            });
        }


        /**
        * Récupère les infos d'un processus LISA
        * @param {string}   codeProcessus  
        * code du processus
        */
        public GetInfoProcessus(codeProcessus: string): IPromesse<Lisa.IInfoProcessus> {
            return this.sendMessage(Messages.LisaMessageSubject.GetInfoProcessus, <Messages.ILisaMessageContentGetInfoProcessus>{
                codeProcessus: codeProcessus
            });
        }
    }

    Components.RegistryAgent.getInstance().register(LisaTechAgent);
} 