module MyWay.Services {
    export interface TechLibrary {
        LisaTechAgent: Lisa.LisaTechAgent;

        StartProcess: (processCode: string, initData: MyWay.Services.Lisa.IInitialisationData, valeurDepart?: number) => IPromesse<Lisa.LisaCallbackResult>;
        StartSubProcess: (processCode: string, valeurDepart?: number)=> IPromesse<Lisa.LisaCallbackResult>;
        StartExternalApp: (name: string, url: string, standAlone?: boolean)=> IPromesse<void>;
        GetProcesses: ()=> IPromesse<Lisa.ProcessResult[]>;
        GetTools: ()=> IPromesse<Lisa.ApplicationResult[]>;
        GetCodesProcessus: ()=> IPromesse<string[]>;
        GetInfoProcessus: (codeProcessus: string) => IPromesse<Lisa.IInfoProcessus>;
    }

    Services.TechLibrary.addInitListener((pInstance: TechLibrary, library: Components.ILibraryTechAgent) => {
        pInstance.LisaTechAgent = library.Lisa;
    });
    
    Services.TechLibrary.prototype.StartProcess = function (processCode: string, initData: MyWay.Services.Lisa.IInitialisationData, valeurDepart?: number): IPromesse<Lisa.LisaCallbackResult>{
        return (<Services.TechLibrary>this).LisaTechAgent.StartProcess(processCode, initData, valeurDepart);
    }

    Services.TechLibrary.prototype.StartSubProcess = function (processCode: string, valeurDepart?: number): IPromesse<Lisa.LisaCallbackResult> {
        return (<Services.TechLibrary>this).LisaTechAgent.StartSubProcess(processCode, valeurDepart);
    }

    Services.TechLibrary.prototype.StartExternalApp = function (name: string, url: string, standAlone?: boolean): IPromesse<void> {
        return (<Services.TechLibrary>this).LisaTechAgent.StartExternalApp(name, url, standAlone);
    }

    Services.TechLibrary.prototype.GetProcesses = function (): IPromesse<Lisa.ProcessResult[]> {
        return (<Services.TechLibrary>this).LisaTechAgent.GetProcesses();
    }

    Services.TechLibrary.prototype.GetTools = function (): IPromesse<Lisa.ApplicationResult[]> {
        return (<Services.TechLibrary>this).LisaTechAgent.GetTools();
    }

    /**
    * Récupère les codes des processus LISA
    */
    Services.TechLibrary.prototype.GetCodesProcessus = function (): IPromesse<string[]> {
        return (<Services.TechLibrary>this).LisaTechAgent.GetCodesProcessus();
    }

    /**
    * Récupère les infos d'un processus LISA
    * @param {string}   codeProcessus  
    * code du processus
    */
    Services.TechLibrary.prototype.GetInfoProcessus = function (codeProcessus: string): IPromesse<Lisa.IInfoProcessus> {
        return (<Services.TechLibrary>this).LisaTechAgent.GetInfoProcessus(codeProcessus);
    }
}