module MyWay.Services {
    export interface TechAgentExtended {
        LisaTechAgent: Lisa.LisaTechAgent;
        
        StartProcess: (processCode: string, initData: MyWay.Services.Lisa.IInitialisationData, valeurDepart?: number) => IPromesse<Lisa.LisaCallbackResult>;
        StartSubProcess: (processCode: string, valeurDepart?: number) => IPromesse<Lisa.LisaCallbackResult>;
        StartExternalApp: (name: string, url: string, standAlone?: boolean) => IPromesse<void>;
        GetProcesses: () => IPromesse<Lisa.ProcessResult[]>;
        GetTools: () => IPromesse<Lisa.ApplicationResult[]>;
        GetCodesProcessus: () => IPromesse<string[]>;
        GetInfoProcessus: (codeProcessus: string) => IPromesse<Lisa.IInfoProcessus>;
    }

    Core.defineProperty(Services.TechAgentExtended, 'LisaTechAgent', {
        get: function () {
            return (<Services.TechAgent>this)._techLibrary.LisaTechAgent
        }
    });

    Services.TechAgentExtended.prototype.StartProcess = function (processCode: string, initData: MyWay.Services.Lisa.IInitialisationData, valeurDepart?: number) {
        return (<Services.TechAgentExtended>this)._techLibrary.StartProcess(processCode, initData, valeurDepart);
    }

    Services.TechAgentExtended.prototype.StartSubProcess = function (processCode: string, valeurDepart?: number) {
        return (<Services.TechAgentExtended>this)._techLibrary.StartSubProcess(processCode, valeurDepart);
    }

    Services.TechAgentExtended.prototype.StartExternalApp = function (name: string, url: string, standAlone?: boolean) {
        return (<Services.TechAgentExtended>this)._techLibrary.StartExternalApp(name, url, standAlone);
    }

    Services.TechAgentExtended.prototype.GetProcesses = function (): IPromesse<Lisa.ProcessResult[]> {
        return (<Services.TechAgentExtended>this)._techLibrary.GetProcesses();
    }

    Services.TechAgentExtended.prototype.GetTools = function (): IPromesse<Lisa.ApplicationResult[]> {
        return (<Services.TechAgentExtended>this)._techLibrary.GetTools();
    }

    /**
    * Récupère les codes des processus LISA
    */
    Services.TechAgentExtended.prototype.GetCodesProcessus = function (): IPromesse<string[]> {
        return (<Services.TechAgentExtended>this)._techLibrary.GetCodesProcessus();
    }

    /**
    * Récupère les infos d'un processus LISA
    * @param {string}   codeProcessus  
    * code du processus
    */
    Services.TechAgentExtended.prototype.GetInfoProcessus = function (codeProcessus: string): IPromesse<Lisa.IInfoProcessus> {
        return (<Services.TechAgentExtended>this)._techLibrary.GetInfoProcessus(codeProcessus);
    }
}