

module MyWay.Services.Components {
    export interface ILibraryTechAgent {
        TraceSuivi: TraceSuivi.TraceSuiviTechAgent;
    }
}

/*
* Module de gestion des traces coté agent.
* @class TraceAgent : initialise l'agent de trace
* @class Logger : Permet de tracer coté manager
*/
module MyWay.Services.TraceSuivi {

    export class TraceSuiviTechAgent extends TraceSuiviAgent {

        /*
        * Appelé par le ServiceAgent
        * @param agentLibrary : services disponibles pour un agent
        * @param errorCallback : gestionnaire d'erreur du serviceAgent
        * @param application : nom de l'application fonctionnelle
        * @param applicationVersion : version de l'application fonctionnelle
        */
        public constructor() {
            super();
        }

        public forcerTrace(trace: MyWay.Services.TraceSuivi.TraceSuiviModel): IPromesse<void> {
            return this.sendMessage<void>(Messages.TraceSuiviMessageSubject.SendTraceSuiviForcee, <Messages.ITraceSuiviMessageContentSendTraceSuiviForcee>{
                trace: trace
            });
        }
    }

    Components.RegistryAgent.getInstance().register(TraceSuiviTechAgent);
}  