module MyWay.Services {
    export interface TechLibrary {
        ContextTechAgent: Context.ContextTechAgent;

        AddToGlobalContext: (kvp: MyWay.Services.Context.IContextKeyValuePair[]) => IPromesse<any>;
        AddToTempoContext: (kvp: MyWay.Services.Context.IContextKeyValuePair[]) => IPromesse<any>;
        AddToMainProcessContext: (key: string, value: string) => IPromesse<any>;
        GetFromMainProcessContext: (key: string) => IPromesse<string>;
        GetCurrentMainProcess: () => IPromesse<Context.IProcess>;
    }

    Services.TechLibrary.addInitListener((pInstance: TechLibrary, library: Components.ILibraryTechAgent) => {
        pInstance.ContextTechAgent = library.Context;
    });

    Services.TechLibrary.prototype.AddToGlobalContext = function (kvp: MyWay.Services.Context.IContextKeyValuePair[]): IPromesse<any> {
        return (<TechLibrary>this).ContextTechAgent.AddToGlobalContext(kvp);
    }

    Services.TechLibrary.prototype.AddToTempoContext = function (kvp: MyWay.Services.Context.IContextKeyValuePair[]): IPromesse<any> {
        return (<TechLibrary>this).ContextTechAgent.AddToTempoContext(kvp);
    }

    Services.TechLibrary.prototype.AddToMainProcessContext = function (key: string, value: string): IPromesse<any> {
        return (<TechLibrary>this).ContextTechAgent.AddToMainProcessContext(key, value);
    }

    Services.TechLibrary.prototype.GetFromMainProcessContext = function (key: string): IPromesse<string> {
        return (<TechLibrary>this).ContextTechAgent.GetFromMainProcessContext(key);
    }

    Services.TechLibrary.prototype.GetCurrentMainProcess = function (): IPromesse<Context.IProcess> {
        return (<TechLibrary>this).ContextTechAgent.GetCurrentMainProcess();
    }
}