module MyWay.Services {
    export interface TechAgent {
        ContextTechAgent: Context.ContextTechAgent;
        
        AddToGlobalContext: (kvp: MyWay.Services.Context.IContextKeyValuePair[]) => IPromesse<any>;
        AddToTempoContext: (kvp: MyWay.Services.Context.IContextKeyValuePair[]) => IPromesse<any>;
        AddToMainProcessContext: (key: string, value: string) => IPromesse<any>;
        GetFromMainProcessContext: (key: string) => IPromesse<string>;
        GetCurrentMainProcess: () => IPromesse<Context.IProcess>;
    }

    Core.defineProperty(Services.TechAgent, 'ContextTechAgent', {
        get: function () {
            return (<Services.TechAgent>this)._techLibrary.ContextTechAgent
        }
    });

    Services.TechAgent.prototype.AddToGlobalContext = function (kvp: MyWay.Services.Context.IContextKeyValuePair[]): IPromesse < any > {
        return this.ContextTechAgent.AddToGlobalContext(kvp);
    }

    Services.TechAgent.prototype.AddToTempoContext = function (kvp: MyWay.Services.Context.IContextKeyValuePair[]): IPromesse < any > {
        return this.ContextTechAgent.AddToTempoContext(kvp);
    }

    Services.TechAgent.prototype.AddToMainProcessContext = function (key: string, value: string): IPromesse < any > {
        return this.ContextTechAgent.AddToMainProcessContext(key, value);
    }

    Services.TechAgent.prototype.GetFromMainProcessContext = function (key: string): IPromesse < string > {
        return this.ContextTechAgent.GetFromMainProcessContext(key);
    }

    Services.TechAgent.prototype.GetCurrentMainProcess = function (): IPromesse < Context.IProcess > {
        return this.ContextTechAgent.GetCurrentMainProcess();
    }
}