

module MyWay.Services.Components {
    export interface ILibraryTechAgent {
        Context: Context.ContextTechAgent;
    }
}

module MyWay.Services.Context {

    export class ContextTechAgent extends ContextAgent {

        constructor() {
            super();
        }

        /**
         * Renvoie toutes les variables partagées
         * @return {} [description]
         */
        GetSharedStorage(): IPromesse<any> {
            return this.sendMessage(ContextMessageSubject.GetSharedStorage, <IContextMessageContentGetSharedStorage>{
            });
        }

        /**
         * Renvoie toutes les variables processus hors portail
         * @return {} [description]
         */
        GetWaitingProcesses(): IPromesse<any> {
            return this.sendMessage(ContextMessageSubject.GetWaitingProcesses, <IContextMessageContentGetWaitingProcesses>{
            });
        }

        /**
         * Renvoie toutes les variables du portail
         * @return {} [description]
         */
        GetPortals(): IPromesse<any> {
            return this.sendMessage(ContextMessageSubject.GetPortals, <IContextMessageContentGetPortals>{
            });
        }

        /**
         * Renvoie toutes les variables du global storage
         * @return {} [description]
         */
        GetGlobalStorage(): IPromesse<IContextKeyValuePair[]> {
            return this.sendMessage(ContextMessageSubject.GetGlobalStorage, <IContextMessageContentGetGlobalStorage>{
            });

        }

        /**
         * Efface le contexte
         * @return {} [description]
         */
        ClearContext(): IPromesse<any> {
            return this.sendMessage(ContextMessageSubject.ClearContext, <IContextMessageContentClearContext>{
            });
        }

        /**
        * Efface le bus interop
        * @return {} [description]
        */
        ClearBus(): IPromesse<any> {
            if (this.library['Interop']) {
                return (<any>this.library['Interop']).clearBus();
            } else {
                return this.library.Core.getPromesseSelfResoved();
            }
        }

        AddToContext(key: string, value: string): IPromesse<boolean>;
        AddToContext(level: MyWay.Services.Context.StorageLevel, kvp: MyWay.Services.Context.IContextKeyValuePair[], elementId?: string): IPromesse<any>;
        AddToContext(arg1: any, arg2: any, elementId?: string): IPromesse<any> {
            if (typeof arg2 === "string") {
                return super.AddToContext(arg1, arg2);
            }
            return this.sendMessage(ContextMessageSubject.AddToContextTech, <IContextMessageContentAddToContextTech>{
                level: arg1,
                kvp: arg2,
                elementId: elementId
            });
        }

        AddToGlobalContext(kvp: MyWay.Services.Context.IContextKeyValuePair[]): IPromesse<any> {
            return this.sendMessage(ContextMessageSubject.AddToGlobalContext, <IContextMessageContentAddToGlobalContext>{
                kvp: kvp
            });
        }

        AddToTempoContext(kvp: MyWay.Services.Context.IContextKeyValuePair[]): IPromesse<any> {
            return this.sendMessage(ContextMessageSubject.AddToTempoContext, <IContextMessageContentAddToTempoContext>{
                kvp: kvp
            });
        }

        AddToMainProcessContext(key: string, value: string): IPromesse<any> {
            return this.sendMessage(ContextMessageSubject.AddToMainProcessContext, <IContextMessageContentAddToMainProcessContext>{
                key: key,
                value: value
            });
        }

        AddToProcessStepContext(key: string, value: string, processStepId: string): IPromesse<any> {
            return this.sendMessage(ContextMessageSubject.AddToProcessStepContext, <IContextMessageContentAddToProcessStepContext>{
                key: key,
                value: value,
                processStepId: processStepId
            });
        }

        GetFromMainProcessContext(key: string): IPromesse<string> {
            return this.sendMessage(ContextMessageSubject.GetFromMainProcessContext, <IContextMessageContentGetFromMainProcessContext>{
                key: key
            });
        }

        GetCurrentMainProcess(): IPromesse<Context.IProcess> {
            return this.sendMessage(ContextMessageSubject.GetCurrentMainProcess, <IContextMessageContentGetCurrentMainProcess>{
            });
        }
    }

    Components.RegistryAgent.getInstance().register(ContextTechAgent);
}
 