

module MyWay.Services.Components {
    export interface ILibraryTechAgent {
        Loggers: Loggers.LoggerTechAgent;
    }
}

module MyWay.Services.Loggers {


    export class LoggerTechAgent extends Components.ComponentAgentBase<ILoggersMessage, LoggersMessageSubject, ILoggersMessageContent> {

        /**
        * Constructeur de la classe LoggerTechAgent
        */
        constructor() {
            super(Components.ComponentType.Loggers);
        }

        public getConfigurationDB(pNom: string): IPromesse<IConfig> {
            return this.sendMessage(LoggersMessageSubject.GetConfigDB, <ILoggersMessageContentGetConfigDB>{
                nom: pNom
            }).then((pReponse: Messages.IGetConfigDBResponse) => {
                return pReponse.config;
            });
        }

        public setConfigurationDB(pNom: string, pConfiguration: IConfig): IPromesse<void> {
            return this.sendMessage<void>(LoggersMessageSubject.SetConfigDB, <ILoggersMessageContentSetConfigDB>{
                nom: pNom,
                config: pConfiguration
            });
        }

        public getConfigurationActual(): IPromesse<IConfig> {
            return this.sendMessage(LoggersMessageSubject.GetConfigActual, <ILoggersMessageContentGetConfigActual>{
            }).then((pReponse: Messages.IGetConfigActualResponse) => {
                return pReponse.config;
            });
        }

        public setConfigurationActual(pConfiguration: IConfig): IPromesse<void> {
            return this.sendMessage<void>(LoggersMessageSubject.SetConfigActual, <ILoggersMessageContentSetConfigActual>{
                config: pConfiguration
            });
        }

        public loadConfiguration(pNom: string): IPromesse<void> {
            return this.sendMessage<void>(LoggersMessageSubject.LoadConfig, <ILoggersMessageContentLoadConfig>{
                nom: pNom
            });
        }

        public saveConfiguration(pNom: string): IPromesse<void> {
            return this.sendMessage<void>(LoggersMessageSubject.SaveConfig, <ILoggersMessageContentSaveConfig>{
                nom: pNom
            });
        }

        public getLoggers(): IPromesse<string[]> {
            return this.sendMessage(LoggersMessageSubject.GetLoggers, <ILoggersMessageContentGetLoggers>{
            }).then((pReponse: Messages.IGetLoggersResponse) => {
                return pReponse.loggers;
            });
        }

        public getNomConfigActuelle(): IPromesse<string> {
            return this.sendMessage(LoggersMessageSubject.GetNomConfigAcuelle, <ILoggersMessageContentGetNomConfigAcuelle>{
            }).then((pReponse: Messages.IGetNomConfigActuelleResponse) => {
                return pReponse.nom;
            });
        }

        public isClassExists(pNom: string): IPromesse<boolean> {
            return this.sendMessage(LoggersMessageSubject.IsClassExists, <ILoggersMessageContentIsClassExists>{
                nom: pNom
            }).then((pReponse: Messages.IIsClassExistsResponse) => {
                return pReponse.isExists;
            });
        }

        public reset(): IPromesse<void> {
            return this.sendMessage<void>(LoggersMessageSubject.Reset, <ILoggersMessageContentReset>{});
        }
    }

    Components.RegistryAgent.getInstance().register(LoggerTechAgent);
}  