
module MyWay.Services {
    export class TechAgentProvider {
        static $inject: string[] = [
            "$q",
            "instanceLoaderService",
            "$timeout",
            "$rootScope"
        ];

        //constructor(private $q: ng.IQService, private instanceLoaderService: MyWay.Services.InstanceLoaderService) {
        //    super();
        //}

        private static _techAgentExtended: TechAgentExtended = null;

        constructor(private $q: ng.IQService, private instanceLoaderService: MyWay.Services.InstanceLoaderService, private $timeout, private $rootScope) {
        }

        public static Instance($q: ng.IQService, instanceLoaderService: MyWay.Services.InstanceLoaderService, $timeout, $rootScope): TechAgentExtended {
            if (TechAgentProvider._techAgentExtended === null) {
                TechAgentProvider._techAgentExtended = new TechAgentExtended($q, instanceLoaderService, $timeout, $rootScope);
            }
            return TechAgentProvider._techAgentExtended;
        }
        init(serviceInit?: IServiceAgentInitialisation): ng.IPromise<TechAgentExtended> {
            var deferred: ng.IDeferred<TechAgentExtended> = this.$q.defer<TechAgentExtended>();
            TechAgentProvider.Instance(this.$q, this.instanceLoaderService, this.$timeout, this.$rootScope).init(serviceInit).then(() => {
                deferred.resolve(TechAgentProvider.Instance(this.$q, this.instanceLoaderService, this.$timeout, this.$rootScope));
            }, () => {
                deferred.reject();
            });
            return deferred.promise;
        }
    }

    export class TechLibrary {
        //Members
        LoggerTechAgent: Loggers.LoggerTechAgent;
        private _errorCallback;
        constructor() {
        }

        private static onInitListener: Array<(pInstance: TechLibrary, library: Components.ILibraryTechAgent) => void> = [];
        public static addInitListener(pCallback: (pInstance: TechLibrary, library: Components.ILibraryTechAgent) => void) {
            TechLibrary.onInitListener.push(pCallback);
        }
        public static removeInitListener(pCallback: (pInstance: TechLibrary, library: Components.ILibraryTechAgent) => void) {
            CoreCommon.ArrayUtils.remove(TechLibrary.onInitListener, (pCurrentListener: (pInstance: TechLibrary, library: Components.ILibraryTechAgent) => void) => {
                return pCurrentListener === pCallback;
            });
        }

        init(library: Components.ILibraryTechAgent, serviceInit?: IServiceAgentInitialisation): IPromesse<any> {
            var deferred: IDeferred<any> = CoreCommon.ServiceDePromesse.defer<any>();
            this._errorCallback = deferred.reject;
            this.LoggerTechAgent = library.Loggers;
            TechLibrary.onInitListener.forEach((pOnInitListener) => {
                pOnInitListener(this, library);
            });
            deferred.resolve();
            return deferred.promise;
        }
    }
    export class TechAgent extends ServiceAgent {
        //Members
        public _techLibrary: TechLibrary; //Public pour externaliser les méthodes de context, lisa, ... .... Moche!
        //Constructor
        constructor() {
            super();
            this._techLibrary = new TechLibrary();
        }
        init(serviceInit?: IServiceAgentInitialisation): IPromesse<any> {
            var deferred: IDeferred<any> = CoreCommon.ServiceDePromesse.defer<any>();
            super.init(serviceInit).then(() => {
                return this._techLibrary.init(<Components.ILibraryTechAgent> this.Library, serviceInit);
            }).then(() => {
                deferred.resolve();
            }).catch(deferred.reject);
            return deferred.promise;
        }

        get LoggerTechAgent(): Loggers.LoggerTechAgent {
            return this._techLibrary.LoggerTechAgent;
        }

        //Raccourcis

        SendRequest(settings: IRequestSettingsExtended): IPromesse<any> {
            return super.SendRequest(settings);
        }
    }

    export class TechAgentExtended extends ServiceAgentExtended {
        public _techLibrary: TechLibrary

        static $inject: string[] = [
            "$q",
            "instanceLoaderService",
            "$timeout",
            "$rootScope"
        ];
        constructor($q: ng.IQService, instanceLoaderService: MyWay.Services.InstanceLoaderService, $timeout, $rootScope) {
            super($q, instanceLoaderService, $timeout, $rootScope);
            this._techLibrary = new TechLibrary();
        }

        /**
         * @ngdoc method
         * @name init
         * @methodOf moduleAgent.TechAgentExtended
         * @description
         * Permet l'initialisation du ServiceAgent.
         * @param {Object} serviceInit Objet contenant le code LISA de la SPA 
         *      et une fonction de rappel en cas de notification du portail vers l'application
         * @returns {Object} Result promise.
         */
        init(serviceInit: IServiceAgentInitialisation): IPromesse<any> {
            var deferred: IDeferred<any> = CoreCommon.ServiceDePromesse.defer<any>();

            super.init(serviceInit).then(() => {
                return this._techLibrary.init(<Components.ILibraryTechAgent>this.Library, serviceInit);
            }).then(() => {
                deferred.resolve();
            }).catch(deferred.reject);

            return deferred.promise;
        }

        get LoggerTechAgent(): Loggers.LoggerTechAgent {
            return this._techLibrary.LoggerTechAgent;
        }

        //Raccourcis

        SendRequest(settings: IRequestSettingsExtended): IPromesse<any> {
            return super.SendRequest(settings);
        }
    }

    if (typeof angular != 'undefined') {
        (<any>moduleAgent).provider('techAgentExtended', function () {
            this.$get = ["$q", "instanceLoaderService", "$timeout", "$rootScope", function ($q, instanceLoaderService, $timeout, $rootScope) {
                return TechAgentProvider.Instance($q, instanceLoaderService, $timeout, $rootScope);
            }]
        });

        (<any>moduleAgent).provider('techAgent', function () {
            this.$get = ["$q", "instanceLoaderService", "$timeout", "$rootScope", function ($q, instanceLoaderService, $timeout, $rootScope) {
                return TechAgentProvider.Instance($q, instanceLoaderService, $timeout, $rootScope);
            }]
        });

        (<any>moduleAgent).provider('serviceAgent', function () {
            this.$get = ["$q", "instanceLoaderService", "$timeout", "$rootScope", function ($q, instanceLoaderService, $timeout, $rootScope) {
                return TechAgentProvider.Instance($q, instanceLoaderService, $timeout, $rootScope);
            }]
        });

        (<any>moduleAgent).provider('serviceAgentExtended', function () {
            this.$get = ["$q", "instanceLoaderService", "$timeout", "$rootScope", function ($q, instanceLoaderService, $timeout, $rootScope) {
                return TechAgentProvider.Instance($q, instanceLoaderService, $timeout, $rootScope);
            }]
        });
    }
}