        private listenersNotificationsCorrespondance: Array<{ callbackInterne: (notification: NotificationMessage) => void, callbackExterne: (notification: NotificationMessage, agent?: IServiceAgent) => void }> = [];

        public SubscribeToNotification(onNotifications: { (notification: NotificationMessage, agent?: IServiceAgent): void }[], serviceAgent: IServiceAgent): IPromesse<any> {
            onNotifications.forEach((pCallbackExterne) => {
                let lIsFound: boolean = false;
                this.listenersNotificationsCorrespondance.every((pCallbackExistant: { callbackInterne: (notification: NotificationMessage) => void, callbackExterne: (notification: NotificationMessage, agent?: IServiceAgent) => void }) => {
                    if (pCallbackExistant.callbackExterne === pCallbackExterne) {
                        lIsFound = true;
                    }
                    return !lIsFound;
                });
                if (!lIsFound) {
                    const lNewListener: { callbackInterne: (notification: NotificationMessage) => void, callbackExterne: (notification: NotificationMessage, agent?: IServiceAgent) => void } = {
                        callbackExterne: pCallbackExterne,
                        callbackInterne: (notification: NotificationMessage) => {
                            pCallbackExterne(notification, serviceAgent);
                        }
                    }
                    this.library.Core.addListenerOnNotification(lNewListener.callbackInterne);
                    this.listenersNotificationsCorrespondance.push(lNewListener);
                }
            });

            if (serviceAgent && this.library['Interop']) {
                return (<any>this.library['Interop']).triggerNotifications();
            } else {
                return Promesses.getPromesseSelfResoved();
            }
        }

        public retirerListenerNotification(onNotificationASupprimer: (notification: NotificationMessage, agent?: IServiceAgent) => void): void {
            CoreCommon.ArrayUtils.remove(this.listenersNotificationsCorrespondance, (pCurrent: { callbackInterne: (notification: NotificationMessage) => void, callbackExterne: (notification: NotificationMessage, agent?: IServiceAgent) => void }) => {
                if (pCurrent.callbackExterne === onNotificationASupprimer) {
                    this.library.Core.removeListenerOnNotification(pCurrent.callbackInterne);
                    return true;
                }
                return false;
            })
        }

